@import '../styles/templates-styles/variables/hotspots.scss';

.slick-slider{
  margin:0 auto;
}

.slider {
  margin: 0 auto;
  position: relative;
  height: 100%;

  .slider-item {
    height: 100vh;

    .slider__title, .slider__text, .slider__complex-info-btn{
      position: absolute;
      z-index: 999;
      margin-left:100px;
    }
    .slider__title {
      font-size: 18px;
      color: #A68E52;
      font-weight: normal;
      
      bottom: 65%;
    }
    
    .slider__text{
      font-size: 68px;
      font-weight: 500;
      color: white;
      width:400px;

      bottom: 35%;
    
    }
    .slider__text.flats{
    }
    .slider__complex-info-btn{
      width: 180px;
    
      padding:16px;
      bottom:30%;
    
      background: #A68E52;
      border-radius: 3px;
      
      
      text-align: center;
    
      color:white;
      font-size: 12px;
      font-weight: 100;
      text-transform: uppercase;
    
    }
    .slider__image {
      width: 100%;
      height: 100vh;
    }
  }
  .slick-track{
    display: flex;
  }

  .slick-arrow{
    position: absolute;
    bottom: 26%;
    z-index: 100;

    color:transparent;
    background-color: #DDDDDD;

    border:none;

    height:1px;
    width: 75px;
    &:hover{
      cursor: pointer;
    }
  }

  .slick-arrow.slick-prev{
    left:250px;
  }

  .slick-arrow.slick-next{
    left:370px;
  }

  .slick-next:after {
    position: absolute;
    content: "";
    top: -10px;
    right: -20px;
    width: 0;
    height: 0;
    display: inline-block;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 10px solid #DDDDDD;
    border-right: 10px solid transparent;
  }

  .slick-prev:before{
    position: absolute;
      content: "";
      top: -9px;
      left: -15px;
      width: 0;
      height: 0;
      display: inline-block;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
      border-right: 10px solid #DDDDDD;
      border-left: 10px solid transparent;
  }

  .slick-dots{
    display: flex;
    position: absolute;
    list-style: none;
    bottom: 13%;
    left: 100px;
    padding: 0;
    li {
      margin-right: 25px;
    button{
      padding:0;
      width:16px;
      height:16px;
      border-radius: 50%;
      border:none;
      background: #757575;
      color:transparent
    
    }

    }
    li.slick-active{
      button{
        background: #DDDDDD;
        opacity: 0.2;
      }
    }
  }

}


@include for-small-phone-up {
  .slider {
    .slider-item {
      .slider__title, .slider__text, .slider__complex-info-btn{
        margin-left:60px;
      }
      .slider__text {
        font-size: 3em;
      }
      .slider__title {
        bottom: 60%;
        font-weight: bold;
        font-size: 1.6em;
      }
    }

    .slick-dots {
      left:60px;
    }
    .slick-arrow.slick-prev{
      left:180px;
    }
  
    .slick-arrow.slick-next{
      left:290px;
    }
  }
}

